<script>


export default {


  data() {
    return { 
        selectedOption: 'option1',
        selectedOption1: null,
        selectedOption2: 'optionlocal',
      option1Text: '',
      option2Text: '',
      option3Text: '',
    };},


      props: {
     value: Boolean
  },
  computed: {
    show: {
      get () {
        return this.value
      },
      set (value) {
         this.$emit('input', value)
      }
    }
  },
  methods:{
    closeDialog() {
      this.dialog = false;
    },
  }

  
  

 

}
</script>